<template>
  <div class="box d-flex flex-column">
    <div id="login">
      <div id="login-header" class="d-flex flex-column justify-content-center align-items-center">
        <img src="~@/assets/images/logo-login.svg" width="125px" height="21px"/>
        <p class="label">로그인</p>
      </div>
      <div class="flex-fill px-24 d-block">
        <span class="d-flex align-items-center label mt-24"><img src="~@/assets/images/ic-user.svg" width="40px" height="40px"/> 아이디</span>
        <b-form-input autocomplete="off" v-model="id" placeholder="ID를 입력하세요." class="mt-8" v-on:click="input"></b-form-input>
        <span class="d-flex align-items-center label mt-14"><img src="~@/assets/images/ic-pwd.svg" width="40px" height="40px"/> 비밀번호</span>
        <b-form-input ref="pwd" type="password" v-model="pwd" placeholder="패스워드를 입력하세요."  class="mt-8"></b-form-input>
        <b-button class="w-100 mt-24 btn-login" variant="primary" v-on:click="login">로그인</b-button>
      </div>
    </div>
    <div class="d-flex flex-fill w-100 justify-content-end">
      <img src="~@/assets/images/ulsan-logo.svg" class="ulsan"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      show: true,
      id: '',
      pwd: '',
      id_idx: 0,
      pwd_idx: 0,
      id_val: ['u','l','s','a','n','@','s','e','s','u','.','a','i'],
      pwd_val: ['u','l','s','a','n'],
      id_interval: undefined,
      pwd_interval: undefined
    }
  },
  mounted() {
    this.show = true
    setTimeout(() => {
      this.$store.commit('setS', {s0:true, s1:false, s2:false, s3:false, s4:false})
    }, 500)
  },
  destroyed() {
    this.$store.commit('setS', {s0:false, s1:false, s2:false, s3:false, s4:false})
  },
  methods: {
    login: function() {
      this.show = false
      this.$store.commit('login')
      this.$router.push('/live')
    },
    input: function() {
      this.id = ''
      this.id_idx = 0
      this.id_interval = setInterval(() => {
        this.id += this.id_val[this.id_idx]
        this.id_idx++
        if (this.id_idx >= this.id_val.length) {
          clearInterval(this.id_interval)
          setTimeout(() => {
            this.$refs.pwd.focus()
            this.inputPwd()
          }, 400)
        }
      }, 150)
    },
    inputPwd: function() {
      this.pwd = ''
      this.pwd_idx = 0
      this.pwd_interval = setInterval(() => {
        this.pwd += this.pwd_val[this.pwd_idx]
        this.pwd_idx++
        if (this.pwd_idx >= this.pwd_val.length) {
          clearInterval(this.pwd_interval)
        }
      }, 150)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../scss/home.scss";
</style>